/* Login.css */
.login-container {
    max-width: 400px;
    margin: 50px auto;
    padding: 20px;
    background-color: #1d1d1d;
    border-radius: 8px;
    color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  }
  
  h2 {
    text-align: center;
    margin-bottom: 20px;
  }
  
  form div {
    margin-bottom: 15px;
  }
  
  label {
    display: block;
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    padding: 10px;
    background-color: #333;
    border: none;
    border-radius: 4px;
    color: #fff;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #00491a;
    border: none;
    border-radius: 4px;
    color: white;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #008800;
  }
  
  button:disabled {
    background-color: #555;
    cursor: not-allowed;
  }
  
  span {
    text-decoration: underline;
    cursor: pointer;
    color: #1db954;
  }
  
  span:hover {
    color: #1ed760;
  }

.google-signin-button {
    width: 100%;
    padding: 10px;
    background-color: #4285F4;
    border: none;
    border-radius: 4px;
    color: white;
    cursor: pointer;
    margin-top: 10px;
    font-weight: bold;
  }
  
  .google-signin-button:hover {
    background-color: #357ae8;
  }
  
  