/* index.css */

/* Основные стили для темной темы */
body {
  background-color: #474747;
  color: #fff;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

/* Стиль для контейнера Dzetafy */
.dzetafy-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh; /* Высота на весь экран */
  text-align: center;
  font-size: 5rem;
  font-weight: bold;
  color: #1db954; /* Зеленый цвет, как в Spotify */
  transition: transform 0.3s ease-in-out; /* Плавный эффект для скрытия */
}

.dzetafy-container.scrolled {
  transform: translateY(-100px); /* Перемещаем текст вверх */
}

/* Стили для навигации */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #1d1d1d;
  border-bottom: 2px solid #333;
}

nav a {
  color: #fff;
  margin-right: 15px;
}

nav button {
  background-color: #1db954;
  color: white;
  border: none;
  padding: 8px 16px;
  cursor: pointer;
  border-radius: 4px;
}

nav button:hover {
  background-color: #1ed760;
}

/* Стили для страниц */
h1, h2 {
  color: #fff;
  text-align: center;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  margin: 10px 0;
  font-size: 18px;
}

audio {
  margin-left: 10px;
}

.info-container {
  padding: 30px;
  background-color: #1d1d1d;
  color: #fff;
  text-align: center;
  font-size: 1.2rem;
}

.info-container h2 {
  font-size: 2rem;
  color: #1db954;
  margin-bottom: 10px;
}

.info-container p {
  margin-bottom: 15px;
  line-height: 1.6;
}

.info-container p:last-child {
  margin-bottom: 0;
}
